const initState={
    imgs:[
        {
            id:1,
            src:require('../assets/imgs/mm1.jpg'),
            text:''
        }
    ]
}

const imgs = (state=initState.imgs,action)=>{
    console.log('imgs store:')
    console.log(state)
    let index = action.index
    switch(action.type){
        case 'img':
            let data_img = [
                ...state,
                {
                    id:index,
                    src:require(`../assets/imgs/mm${index}.jpg`),
                    text:`${index}`
                }
            ]
            console.log('return imgs store:')
            console.log(data_img)
            return data_img
        case "test":
            let data_test = [{
                    id:index,
                    src:require(`../assets/imgs/mm${index}.jpg`),
                    text:`${index}`
                }]
            console.log('return imgs store:')
            console.log(data_test)
            return data_test
        default:
            return state
    }
}

export default imgs


